.SuperSelectOld_select__3AplO {
    position: relative;
    display: flex;
    width: 15em;
    height: 2em;
    line-height: 3;
    background: darkslategrey;
    overflow: hidden;
    border-radius: .25em;
}
.SuperSelectOld_select__3AplO::after {
    content: '\25BC';
    position: absolute;
    top: 0;
    right: 0;
    padding: 0 1em;
    background:darkslategrey;
    cursor:pointer;
    pointer-events:none;
    transition:.25s all ease;
}
.SuperSelectOld_select__3AplO:hover::after {
    color: darkslategrey;
}
